<template>
  <div class="ssr">
    <div v-for="(item,index) in ssrArrs" :key="index" class="content">
      <textarea name id cols="80" rows="5" v-model="item.ssr" disabled ref="ssr"></textarea>
      <p class="copy" @click="copy(index)" ref="copy">复制</p>
    </div>
  </div>
</template>


<script>
export default {
  data() {
    return {
      ssrArrs: [
        {
          id: 0,
          ssr:
            "ssr://MTk0LjE1Ni4xMjAuMTAyOjg4ODY6YXV0aF9jaGFpbl9hOm5vbmU6cGxhaW46ZDNkM0xtWnlaV1Z6Y3k1aVpYTjAvP3JlbWFya3M9VTFOU1ZFOVBURjlPYjJSbE91U19oT2U5bC1hV3J5MU5iM05qYjNjJnByb3RvcGFyYW09Jm9iZnNwYXJhbT0"
        },
        {
          id: 1,
          ssr:
            "ss://bm9uZTp3d3cuZnJlZXNzLmJlc3RAMTk0LjE1Ni4xMjAuMTAyOjg4ODY#SSRTOOL_Node:%E4%BF%84%E7%BD%97%E6%96%AF-Moscow"
        },
        {
          id: 2,
          ssr:
            "ssr://MTk0LjE1Ni4xMjAuMTAyOjg4ODY6YXV0aF9jaGFpbl9hOm5vbmU6cGxhaW46ZDNkM0xtWnlaV1Z6Y3k1aVpYTjAvP3JlbWFya3M9VTFOU1ZFOVBURjlPYjJSbE91U19oT2U5bC1hV3J5MU5iM05qYjNjJnByb3RvcGFyYW09Jm9iZnNwYXJhbT0"
        }
      ]
    };
  },
  methods: {
    copy(index) {
      let copy = this.$refs.copy;
      let ssrValue = this.$refs.ssr[index].value;
      this.$copyText(ssrValue)
        .then(res => {
          copy[index].innerHTML = "已复制";
          copy[index].style.color = "red";
        })
        .catch(err => {
          alert("Can not copy");
        });

      setTimeout(() => {
        copy[index].innerHTML = "复制";
        copy[index].style.color = "black";
      }, 1000);
    }
  }
};
</script>


<style lang="" scoped>
.ssr {
  margin-top: 20px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.content {
  padding-bottom: 10px;
}
.copy {
  cursor: pointer;
  width: 50px;
  text-align: center;
  border-radius: 8px;
  font-weight: 600;
}
</style>



